import { Link } from "react-router-dom"
import { InputSearchBanner } from "./InputSearch";

import '../../styles/layout/_banner.scss';

import { headerGnbDepth2 } from "../../data/memo";


export default function Banner({ page }) {
  // page default 객체
  const inf = { 
    community: false,
    inputCate : {
      banner: false,
      placeholder: 'default',
    },
    download: false,
    ...page
  };

  // console.log(inf);

  const bannerClass = inf.community ? 'subpage-banner commu' : 'subpage-banner';

  const returnContent = (pageCode = -1) => {
    if (pageCode < 0) { // 개요 페이지 입니다.
      const { h5, span } = headerGnbDepth2[page.code - 4];
      return {
        head: h5, text: span, bannercode: -1
      }
    }
    else { // 서브 페이지 입니다. 
      const { text, p } = ul[pageCode];
      return {
        head: text, text: p, bannercode: pageCode
      }
    }
  };

  const makeulLi = (uls) => {
    return uls.map((li, index) => {
      return <li key={index}>
        <Link to={li.path}
          className={bannercode === index ? 'focused' : ''}
        >{li.text}</Link>
      </li>
    });
  };

  const { ul, img_banner } = headerGnbDepth2[page.code - 4]; // ex) OSP5111 - code: 5 -> 5-4  --> 

  const { head, text, bannercode } = returnContent(page.subcode);

  const uls = makeulLi(ul);

  const pText = text.map((item, index) => {
    return <span key={index}>
      {item}
    </span>
  });



  return <>
    <section className={bannerClass}>
      
      <span className={"illust-subpage " + img_banner}></span>

      {/* 서브 메뉴 */}
      <div className='subpage-depth2'>
        <ul className='depth2__container width1440'>{uls}</ul>
      </div>

      {/* 서브 내용 */}
      <div className='subpage-cont width1440'>
        <h2>{head}</h2>
        <p className={inf.code === 5 && inf.subcode > -1 ? 'important': ' '}>
          {pText}
        </p>

        {inf.download && 
          <a href='#download' className='btn-rounded-02 btn-download-tmaxgr' download={false}>
            다운로드 
          </a>
        }

        {inf.inputCate.banner && <InputSearchBanner page={page} />}
      </div>
      
    </section>
  </>
}