/**
 * 
 * @param {*} param0 
 * @returns 
 */
import { useState, useEffect } from 'react';

import './../../styles/layout/_tabstyle.scss';


const TabBoxedStyle = ({props}) => {
  // tab default값
  const tab = { 
    list: [
      '제품 소개',
      '특장점',
      '활용 분야'
    ],
    id: [
      1, 2, 3
    ],
    page: 'osp4116',
    activeTab: 0,
    ...props
  };

  const [ boxFocus, setBoxFocus] = useState(tab.activeTab);
  const [ tabFix, setTabFix ] = useState(false);

  const onClick = (index = 0) => {
    setBoxFocus(Number(index));
  }

  // bar scroll 시 상단에 붙는 interaction 
  const onScroll = (event) => {
    const header = document.querySelector('.header');

    // ! 페이지 마다 바뀌어야 함
    const osp = document.querySelector(`.main-${tab.page}`);

    const bannerHeight = document.querySelector('.subpage-banner').offsetHeight - 25;

    // console.log(bannerHeight); // 464(web) // 231(mobile)
    // console.log(window.scrollY);

    if(window.scrollY < (bannerHeight)){ // 450
      header.style.transform = `translateY(0%)`;
    }

    else if(window.scrollY > (bannerHeight) && window.scrollY <= (bannerHeight+100)){
      header.style.transform = `translateY(${(bannerHeight)-window.scrollY}%)`;
      setTabFix(false);
      osp.classList.remove('fixed');
    }

    else if(window.scrollY > (bannerHeight+100)){
      header.style.transform = 'translateY(-100%)';

      osp.classList.add('fixed');
      setTabFix(true)
    }

    else{
      osp.classList.remove('fixed');
      setTabFix(false);
    }
  }

  useEffect(() => {
    window.addEventListener('scroll', onScroll);
    return () => {
      window.removeEventListener('scroll', onScroll);
    }
  }, [])


  return <div className={tabFix ?'tab-boxed-fix fixed':'tab-boxed-fix'}>
  <ul 
    className="tab-list"
    // onClick={(event) => onClick(event)}
  >
    {
      tab.list.map((item, index) => {
        return <li key={index} 
          className={index === boxFocus ? 'focused': ' '}
        >
          <a 
            href={'#'+tab.id[index]}
            // value={index}
            onClick={() => onClick(index)}
          >
          {item}</a>
        </li>
      })
    }
  </ul>
</div>
}


/**
 * * 바 스타일의 Tab
 * @exports to OSP4111 (TmaxOS 소개)
 * 
 * todo: setTabNumber랑 이런거 default 값이 있어야 할거같은데...
 */

const TabBarStyle = ({props}) => {

  const tab = {
    list: [
      'tab01',
      'tab02',
      'tab03'
    ],
    commu: false,
    alert: false,
    ...props
  };


  const onClick = (event) => {
    const { target: { value } } = event;
    
    if (value) {
      props.setTabNumber(Number(value));
    }
  }

  return <div className={tab.commu ? 'tab__bar-style commu' : 'tab__bar-style'}>
  <ul className='width1440'
    onClick={(event) => onClick(event)}
  >
    {
      tab.list.map((item, index) => {
        return <li key={index}>
          <button
            value={index}
            className={props.tabNumber === index ? 'focused' : ''}
          >{item}

          {/* 임시로 놔둠  */}
          <span className={props.alert && index === 2 ? "alert-new": "alert-new --n"}>2</span>
          </button></li>
      })
    }
  </ul>
</div>
}

export { TabBoxedStyle, TabBarStyle };